.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.row {
  display: flex;
}

.square {
  width: 80px;
  height: 80px;
  border: 2px solid #c9c9c9;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  background-color: burlywood;
}

.square:hover {
  background-color: #e5e5e5;
}

.status {
  font-size: 24px;
  margin-bottom: 10px;
}

.reset {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #f44336;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.reset:hover {
  background-color: #c62828;
}
body{
  background-color:lightcoral;
}